<template>
		<h2>오브젝트 데이터의 내부를 확인하는 예제</h2>
		<div id="app">
			<p>{{ myText }}</p>
			<p v-text="myText"></p>
			<p v-html="myHtmlText"></p>
			<p> 숫자 {{ myNumber }}</p>
			<p> 부울형 {{ myBool }}</p>
			<p> 배열 {{ myArray1 }}</p>
			
            <h2>입력폼 활용</h2>
            </div>
		<div id="app">
			이름 : <input v-model.lazy="myName" placeholder="이름"> <br />

			좋아하는 색은 : <br />
			<label><input type="radio" value="red" v-model="myChecks">
				빨강</label><br>
			<label><input type="radio" value="green" v-model="myChecks">
				녹색</label><br>
			<label><input type="radio" value="blue" v-model="myChecks">
				파랑색</label><br>
			<label><input type="radio" value="yellow" v-model="myChecks">
				노란색</label><br>
			<label><input type="radio" value="gray" v-model="myChecks">
				회색</label><br>
			<p v-bind:style="{color : myChecks}">내 이름은 {{ myName }} 이고 좋아하는 색은 {{myChecks}} 입니다.</p>
		</div>


        <div id="app">
			<input v-model="myName" placeholder="이름">
			<p>나는 {{ myName }} 입니다.</p>
		</div>


        <h2>[Shift] + [Enter] 키를 누르면 얼럿을 표시하는 예제</h2>
		<div id="app">
			<input v-on:keyup.enter.shift="showAlert" v-model="myText2">
			<p>{{ myTex2t }}</p>
		</div>

        <h2>클릭하면 지정된 값만큼 증가 시키는 예제</h2>
		<div id="app">
			<p>{{ count }}</p> 
			<button v-on:click="countUp(3)">3증가</button>
			<button v-on:click="countUp(10)">10증가</button>
			<button v-on:click="countUp(100)">100증가</button>
		</div>

        <h2>배열 데이터를 번호가 붙어 있는 리스트로 표시 하는 예제 </h2>
		<div id="app">
			<ul>
				<li v-for="(item, index) in myArray2" v-bind:key="item"> {{ index }}:{{ item }}</li>
			</ul>
		</div>

        <h2>버튼을 클릭하면 소트하는 예제</h2>
		<div id="app">
			<ul>
				<li v-for="item in myArray3" v-bind:key="item"> {{ item }}</li>
			</ul>
			<button v-on:click="sortData2(myArray3)">소트</button><br>
		</div>

        <h2>숫자 배열 조작과 구구단 출력</h2>
<div id="app">
    <ul>
        <li v-for="(item, index) in myArray" v-bind:key="item">{{ item }}
            <button v-on:click="modifyNumber(index)">변경</button>
            <button v-on:click="deleteNumber(index)">삭제</button>
        </li>
        <br/>
        <button v-on:click="addNumber()">추가</button>
        <button v-on:click="sortData()">소트</button>
        <button v-on:click="showGuGu()">구구단 보이기</button>

        <ul v-if="show">
            <li v-for="item in myArray" v-bind:key="item">
                <p v-for="item2 in 9" v-bind:key="item2">{{item}}x{{item2}}={{item * item2}}</p>
            </li>
        </ul>
    </ul>
</div>
</template>
<script>
export default {
    data() {
        return {
            myText:'Vue 를 배워 봅시다. 이렇게 그냥 출력할수도 있고',
			myHtmlText:'<h3> 태그를 넣어서 출력할 수도 있습니다.</h3>',
			myNumber:12345,
			myBool:true,
			myArray1:[1,2,3,4,5],
            myName:'',
			myChecks:[],
            myText2: 'Hello!',
            count:0,
            myArray2: ['쨈빵','멜론빵','크로와사','슈크림빵'],
            myArray3: ['one','two','three','four','five'],
            myArray: [2,3,4,5,6,7,8,9],
            show:false
        }
    },
    methods: {
					showAlert: function() {
						alert("Shift + Enter 를 눌렀습니다.");
					},
                    countUp: function(value) {
						this.count += value;
					},
                    sortData2: function(listdata) {
						listdata.sort(function(a,b) {
							return (a < b ? -1 : 1);
						});
					},
                    sortData: function () {
                this.myArray = this.myArray.filter (function (item, idx, array) {
                    return array.indexOf(item) === idx;
                });

                this.myArray.sort(function(a,b) {
                    return (a < b ? -1 : 1);
                });
            },
            addNumber: function() {
              this.myArray.push(1);
            },
            modifyNumber: function(index) {
                this.myArray.splice(index, 1, this.myArray[index] + 1);
            },
            deleteNumber: function(index) {
                this.myArray.splice(index, 1);
            },
            showGuGu: function() {
                this.show = true;
            }				
				}
}
</script>